.main-nav {
  position: relative;

  @media (min-width: $tablet-width) {
    max-width: 70%;
  }
}

.main-nav__list {
  @include list-reset;

  @media (max-width: $mobile-width-only) {
    background-color: #ffffff;
  }

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: end;
    column-gap: 33px;
    row-gap: 10px;
    flex-wrap: wrap;
  }
}

.main-nav__item {
  position: relative;

  @media (max-width: $mobile-width-only) {
    text-align: center;
    padding: 22px 0 24px 0;
    border-top: 1px solid #e6e6e6;
  }

  @media (min-width: $tablet-width) {
    &--active::after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $primary-color;
    }
  }
}

.main-nav__link {
  font-family: $accent-font;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;

  @media (min-width: $tablet-width) {
    font-size: 18px;
    line-height: 24px;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 24px;
  }
}


.main-nav__toggle {
  @media (max-width: $mobile-width-only) {
    position: absolute;
    top: -37px;
    right: 0;
    background: transparent;
    border: none;
    padding: 0;
    width: 24px;
    height: 24px;
    cursor: pointer;

    &--closed {
      &::before {
        content: "";
        display: block;
        height: 2px;
        background-color: #000000;
        box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
      }

      + .main-nav__list {
        display: none;
      }
    }

    &--opened {
      &::before {
        content: "";
        position: relative;
        display: block;
        height: 20px;
        top: 8px;
        background: url("../img/button-menu-opened.svg") no-repeat right center;
      }

      + .main-nav__list {
        position: absolute;
        min-width: 320px;
        left: -20px;
        right: -20px;
        top: 15px;
        z-index: 10;
      }
    }
  }

  @media (max-width: $mobile-width-only) {
    &--no-js {
      & ~ .main-nav__list {
        margin-top: 10px;
      }
    }
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}
